const images = require.context("../../../public/img/univers", true);

export default function University(props) {
    let imgUrl = `/img/univers/${props.value.logo}.png`;

    return (
        <div class="card mb-3" style="max-width: 1000px;">
        <div class="row g-0">
            <div class="col-md-4">
            <img src={imgUrl} class="img-fluid rounded-start" alt="..." />
            </div>
            <div class="col-md-8">
            <div class="card-body">
                <h5 class="card-title">${props.value.title}</h5>
                <p class="card-text">${props.value.description}</p>
                <p class="card-text"><small class="text-muted">${props.value.location}</small></p>
            </div>
            </div>
        </div>
        </div>
    );
}