import ResultDescriptionBox from "./ResultDescriptionBox";
import ResultDescriptionTypesBox from "./ResultDescriptionTypesBox";

export default function ResultDescription(props) {
    return (
        <div className="row mt-3">
            <div className="col-lg-4 wow fadeInLeft" data-wow-duration=".2s" data-wow-delay=".2s">
                <ResultDescriptionBox header="   :" titleList={props.result.specialties} />
            </div>
            <div className="col-lg-4 wow fadeInLeft" data-wow-duration=".2s" data-wow-delay=".4s">
                <ResultDescriptionTypesBox header="   :" resultList={props.result.results} />
            </div>
            <div className="col-lg-4 wow fadeInLeft" data-wow-duration=".2s" data-wow-delay=".6s">
                <ResultDescriptionBox header="   :" titleList={mapUniversities(props.result)} />
            </div>
        </div>
    );
}

function mapUniversities(json) {
    let universityList = [];

    // get all universities from json
    json.specialties.map(specialty => {
        specialty.universities.map(university => {
            universityList.push({
                id: university.universityId, 
                title: university.title
            });
        });
    });

    // remove duplicates
    universityList = universityList.filter((value, index, self) =>
        index === self.findIndex((t) => t.id === value.id )
    )

    return universityList;
}