import { Component } from "react";
import TestAnswer from "./TestAnswer";
import { API_URL } from '../../config';

export default class MainTestSection extends Component {

    constructor(props) {
        super(props);
   
        this.state = {
            items: [],
            isLoaded: false,
            error: null
        };
    }

    componentDidMount() {
        fetch(API_URL + '/tests/main')
            .then(result => result.json())
            .then(data => {
                this.setState({
                    isLoaded: true,
                    items: data,
                    error: null
                });  
            }, error => {
                this.setState({
                    isLoaded: true,
                    error: error, 
                    items: []
                });
            });
    }

    render() {
        if(this.state.error !== null) {
            return <div>Error while loading ZNO`S</div>
        }

        if(!this.state.isLoaded) {
            return <div>...</div>
        }

        return (
            <div>
                {this.state.items.questions.map(question => {
                    return (
                        <div class="card border-success mt-1">
                            <div class="card-body">
                                <h5 class="card-title">{question.title}</h5>
                                <div className="row form-check ">
                                    {question.answers.map(answer => {
                                        return <TestAnswer text={answer.title} weight={answer.weight} id={question.questionId} answerId={answer.answerId}/>
                                    })}
                                </div>
                            </div>
                        </div>
                    )  
                })}
            </div>
        );
    }
}