import React, { Component } from 'react';
import Stepper from 'bs-stepper';
import ExamSection from './ExamSection';
import MainTestSection from "./MainTestSection";
import $ from 'jquery';
import { API_URL } from '../../config';
import ResultSection from './results/ResultSection';

export default class StepperTest extends Component {
  constructor() {
    super();

    this.state = {
        isLoaded: false,
        items: [],
        error: null,
        isResult: false
    };
  }

  componentDidMount() {
    this.stepper = new Stepper(document.querySelector('#stepper1'), {
      linear: false,
      animation: true
    });

    // to re-render first step content
    this.stepper.next();
    this.stepper.previous();

    this.calculate = this.calculate.bind(this);
  }

  onSubmit(e) {
    e.preventDefault()
  }

  render() {
    if(this.state.isResult) {
        return (
            <ResultSection result={this.state.items}/>
        );
    }

    return (
        <div className="bs-stepper" id="stepper1">
        <a name="maintest"></a>
        <div className="bs-stepper-header" role="tablist">
          <div className="step" data-target="#zno-part">
            <button type="button" className="step-trigger" role="tab" aria-controls="zno-part" id="zno-part-trigger">
              <span className="bs-stepper-circle">1</span>
              <span className="bs-stepper-label"> </span>
            </button>
          </div>
          <div className="line"></div>
          <div className="step" data-target="#main-test-part">
            <button type="button" className="step-trigger" role="tab" aria-controls="main-test-part" id="main-test-part-trigger">
              <span className="bs-stepper-circle">2</span>
              <span className="bs-stepper-label"> </span>
            </button>
          </div>
        </div>
        <div className="bs-stepper-content">
          <div id="zno-part" className="content" role="tabpanel" aria-labelledby="zno-part-trigger">
                <ExamSection />
                <div className="row justify-content-center">
                    <div className="col-auto">
                        <button type="button" className="btn btn-outline-secondary stepper-previous-btn"
                        onClick={() => this.stepper.next()}></button>
                    </div>
                </div>
            </div>
          </div>
          <div id="main-test-part" className="content" role="tabpanel" aria-labelledby="main-test-part-trigger">
            <MainTestSection />
            <div className="row justify-content-center mt-3 mb-3">
                <div className="col-auto">
                    <button type="button" className="btn btn-outline-secondary stepper-previous-btn"
                        onClick={() => this.stepper.previous()}></button>
                </div>
                <div className="col-auto">
                    <input type="submit" className="btn btn-success stepper-previous-btn" value=" " 
                    onClick={() => this.calculate()} />
                </div>
            </div>
          </div>
        </div>
    );
  }

  calculate() {
      let arr = [];
      
      $("#main-test-part input[type=radio]:checked").each(function() {
        if(this.checked) {
            arr.push(parseInt(this.value, 10)); 
        }
      });

      fetch(API_URL + '/tests/main/result', {
        method: "POST",
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(arr)
      })
            .then(result => result.json())
            .then(data => {
                this.setState({
                    isLoaded: true,
                    items: data,
                    error: null,
                    isResult: true
                });  
            }, error => {
                this.setState({
                    isLoaded: true,
                    error: error, 
                    items: [],
                    isResult: true
                });
            });
      // render result page 
  }
}