import slide1 from '../../img/slides/slide1.png';
import slide2 from '../../img/slides/slide2.png';
import slide3 from '../../img/slides/slide3.png';
import slide4 from '../../img/slides/slide4.png';
import slide5 from '../../img/slides/slide5.png';

import SliderHeader from './SliderHeader';

export default function Slider() {
    return (
        <div className="slider mr-2 ml-2">
            <div id="myCarousel" className="carousel slide carousel-fade" data-bs-ride="carousel">
                <div className="carousel-inner">
                    <div className="carousel-item active">
                        <img className="d-block w-100"  src={slide1} width="100%"/>
                        <SliderHeader />
                    </div>
                    <div className="carousel-item">
                        <img className="d-block w-100"  src={slide2} width="100%"/>
                        <SliderHeader />
                    </div>
                    <div className="carousel-item">
                        <img className="d-block w-100"  src={slide3} width="100%"/>
                        <SliderHeader />
                    </div>
                </div>
            </div>
        </div>
    );
}