import React, { Component } from 'react';
import $ from 'jquery';

import Stepper from 'bs-stepper';
import ExamSection from "./ExamSection";
import MainTestSection from "./MainTestSection";
import ResultSection from "./results/ResultSection";
import StepperHeader from "./StepperHeader";

export default class StepperComponent extends Component {
    constructor() {
        super();
        this.componentDidMount = this.componentDidMount.bind(this);
    }

    componentDidMount() {
        this.stepper = new Stepper(document.querySelector('#bs-stepper'), {
            linear: false,
            animation: true
        });

        console.log('mount');
        console.log(this.stepper);

        $('.stepper-next-btn').on('click', () => {
            console.log('next')
            this.stepper.next();
        });

        $('.stepper-previous-btn').on('click', () => {
            console.log('previous')
            this.stepper.previous();
        });
    }

    onSubmit(e) {
        e.preventDefault()
    }

    render() {
        return (
            <div id="bs-stepper">
                <StepperHeader />

                <div className="bs-stepper-content">
                    <div id="zno-part" className="content" role="tabpanel" aria-labelledby="zno-part-trigger">

                        <ExamSection />
                        <div className="row justify-content-center">
                            <div className="col-auto">
                                <button type="button" className="btn btn-primary stepper-next-btn">
                                    <span className="ml-2"><i className="fa fa-angle-right" aria-hidden="true"></i></span> 
                                </button>
                            </div>
                        </div>
                    </div>
                     <div id="test-part" className="content" role="tabpanel" aria-labelledby="test-part-trigger">
                        <MainTestSection />
                        <div className="row justify-content-center">
                            <div className="col-auto">
                                <button type="button" className="btn btn-outline-secondary stepper-previous-btn"> 
                                <span className="mr-2"><i className="fa fa-angle-left"
                                aria-hidden="true"></i></span> </button>
                            </div>
                            <div className="col-auto">
                                <button type="button" className="btn btn-primary stepper-next-btn">
                                    <span className="ml-2"><i className="fa fa-angle-right" aria-hidden="true"></i></span> 
                                </button>
                            </div>
                        </div>
                    </div>
                    <ResultSection />
                </div>
            </div>
        );
    }
}