export default function StepperHeader() {
    return (
        <div className="bs-stepper-header" role="tablist">
            <div className="step" data-target="#zno-part">
                <button type="button" className="step-trigger" role="tab" aria-controls="zno-part"
                        id="zno-part-trigger">
                    <span className="bs-stepper-circle">1</span>
                    <span className="bs-stepper-label"> </span>
                </button>
            </div>
            <div className="line"></div>
            <div className="step" data-target="#test-part">
                <button type="button" className="step-trigger" role="tab" aria-controls="test-part"
                        id="test-part-trigger">
                    <span className="bs-stepper-circle">2</span>
                    <span className="bs-stepper-label"> </span>
                </button>
            </div>
            <div className="line"></div>
            <div className="step" data-target="#result-part">
                <button type="button" className="step-trigger" role="tab" aria-controls="result-part"
                        id="result-part-trigger">
                    <span className="bs-stepper-circle">3</span>
                    <span className="bs-stepper-label"></span>
                </button>
            </div>
        </div>
    );
}