import ResultCathedra from "./ResultCathedra";

export default function ResultCathedras(props) {
    return (
        <div class="row row-cols-1 row-cols-md-2 row-cols-xl-4 g-4">
            {props.cathedras.map(cathedra => 
                <ResultCathedra cathedra={cathedra} />
            )}
        </div>
    );
}