import { Component } from 'react';
import '../../models/card-selector';

export default class ExamItem extends Component {
    constructor(props) {
        super(props);
   
        this.defaultImage = 'https://i.imgur.com/bGCqROr.png';

        this.state = {
            selected: false
        }

        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        this.setState(prevState => ({
            selected: !prevState.selected
        }));
    }

    render() {
        return (
            <div className={"col-auto ml-sm-2 mx-1 card-block  py-0 text-center radio " + (this.state.selected ? 'selected' : '')} onClick={this.handleClick}>
                <div className="flex-row">
                    <div className="col">
                        <div className="pic"> 
                            <img className="irc_mut img-fluid" src={this.props.img ?? this.defaultImage}  width="100" height="100" /> 
                        </div>
                        <input type="hidden" name="examId" value={this.props.id} />
                        <p>{this.props.text}</p>
                    </div>
                </div>
            </div>
        );
    }
    
}