import Chart from "chart.js/auto";
import { useEffect } from "react";

export default function ResultDescriptionTypesBox(props) {
    useEffect(() => {
        // code to run after render goes here
        let resultTitles = [];
        let resultCounts = [];

        props.resultList.map(result => {
            resultTitles.push(result.title);
            resultCounts.push(result.answersCount);
        })

        const ctx = document.getElementById('radarChart');
        const myChart = new Chart(ctx, {
            type: 'bar',
            data: {
                labels: resultTitles,
                datasets: [{
                    label: ' ',
                    data: resultCounts,
                    backgroundColor: [
                        'rgba(255, 99, 132, 0.2)',
                        'rgba(54, 162, 235, 0.2)',
                        'rgba(255, 206, 86, 0.2)',
                        'rgba(75, 192, 192, 0.2)',
                        'rgba(153, 102, 255, 0.2)'
                    ],
                    borderColor: [
                        'rgba(255, 99, 132, 1)',
                        'rgba(54, 162, 235, 1)',
                        'rgba(255, 206, 86, 1)',
                        'rgba(75, 192, 192, 1)',
                        'rgba(153, 102, 255, 1)'
                    ],
                    borderWidth: 1
                }]
            },
            options: {
                scales: {
                    y: {
                        beginAtZero: true
                    }
                }
            }
        });

    }, []); // <-- empty array means 'run once'

    return (
        <div className="result-box">
            <h2>{props.header}</h2>
            <canvas id="radarChart" width="400" height="400"></canvas>
        </div>
    );
}