export default function ResultCathedra(props) {
    return (
        <div class="col">
            <div class="card">
                <img src={props.cathedra.university.img} class="card-img-top" alt="university-logo" />
                <div class="card-body">
                    <h5 class="card-title">{props.cathedra.description} <b>{props.cathedra.title}</b></h5>
                    <p class="card-text">{props.cathedra.university.title}</p>
                </div>
            </div>
        </div>
    );
}