import '../../style.css';
import '../../models/card-selector';
import { Component } from 'react';
import { API_URL } from '../../config';

import ExamItem from './ExamItem';


export default class ExamSection extends Component {
    constructor(props) {
        super(props);
   
        this.state = {
            items: [],
            isLoaded: false,
            error: null
        };
    }

    componentDidMount() {
        fetch(API_URL + '/exams')
            .then(result => result.json())
            .then(data => {
                this.setState({
                    isLoaded: true,
                    items: data,
                    error: null
                }, () => console.log(this.state));  
            }, error => {
                this.setState({
                    isLoaded: true,
                    error: error, 
                    items: []
                }, () => console.log(this.state));
            });
    }

    render() {
        if(this.state.error !== null) {
            return <div>Error while loading ZNO`S</div>
        }

        if(!this.state.isLoaded) {
            return <div>...</div>
        }

        return (
            <div className="row justify-content-center">
                <div className="col-md-9">
                    <div className="card text-center justify-content-center shaodw-lg  card-1 border-0 bg-white px-sm-2">
                        <div className="card-body show  ">
                            <div className="row">
                                <div className="col">
                                    <h5><b></b></h5>
                                    <p>    <span className=" ml-1 cursor-pointer"><a href='https://testportal.gov.ua/'></a></span> </p>
                                </div>
                            </div>
                            <div className="radio-group row justify-content-between px-3 text-center a">
                                {this.state.items.map(exam => {
                                    return <ExamItem text={exam.title} id={exam.id} />
                                })}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}