function mapCathedras(json) {
    let resultCathedraList = [];

    json.specialties.map(specialty => {
        specialty.universities.map(university => {
            university.cathedraList.map(cathedra => {
                resultCathedraList.push({
                    cathedraId: cathedra.cathedraId,
                    title: cathedra.title,
                    description: cathedra.description,
                    university: {
                        id: university.universityId,
                        title: university.title,
                        img: onaftLogo
                    }
                });
            });
        });
    });

    // remove duplicates
    resultCathedraList = resultCathedraList.filter((value, index, self) =>
        index === self.findIndex((t) => t.cathedraId === value.cathedraId )
    );
    
    return resultCathedraList;
}