import { Component } from "react";

export default class TestAnswer extends Component {
    constructor(props) {
        super(props);
   
        this.defaultImage = 'https://i.imgur.com/bGCqROr.png';

        this.state = {
            checked: false
        }

        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        this.setState(prevState => ({
            checked: !prevState.checked
        }));
    }

    render() {
       return (
            <div class="form-check">
                <input className="form-check-input" type="radio" name={"question-" + this.props.id} id={"answer-" + this.props.answerId} value={this.props.answerId} />
                <label className="form-check-label" htmlFor={"answer-" + this.props.answerId}>
                    {this.props.text}
                </label>
            </div>
        ); 
    }
    
}